// src/app/levels/[category]/[id]/page.tsx
import { notFound } from 'next/navigation';
import { Metadata } from 'next';
import data from '@/data/levels.json';
import { slugToCategoryName, categoryNameToSlug } from '@/utils/categoryUtils';

interface Level {
  id: number;
  category: string;
  title: string;
  videoId: string;
  thumb: string;
}

// 动态生成元数据
export async function generateMetadata({ params }: { 
  params: { category: string; id: string } 
}): Promise<Metadata> {
  try {
    const { category, id } = params;
    const levelId = Number(id);
    
    // 将URL slug转换回显示名称
    const displayCategory = slugToCategoryName(category);
    
    // 查找对应的关卡数据
    const levels = data as Level[];
    const level = levels.find(l => 
      l.id === levelId && 
      categoryNameToSlug(l.category) === category
    );
    
    if (!level) {
      return {
        title: `Perfect Tidy Level ${levelId}`,
        description: `Complete walkthrough for Perfect Tidy level ${levelId}`,
      };
    }
    
    return {
      title: `Perfect Tidy Level ${level.id} (${level.category})`,
      description: `Complete walkthrough for ${level.title}. Category: ${level.category}`,
    };
  } catch (error) {
    return {
      title: 'Perfect Tidy Level Guide',
      description: 'Complete walkthrough and solutions for Perfect Tidy levels.',
    };
  }
}

// 生成静态路径参数 - 使用简洁分类名
export async function generateStaticParams() {
  const levels = data as Level[];
  
  return levels.map(level => ({
    category: categoryNameToSlug(level.category), // 使用简洁分类名
    id: String(level.id)
  }));
}

// 详情页
export default async function Page({ params }: { 
  params: { category: string; id: string } 
}) {
  try {
    const { category, id } = params;
    const levelId = Number(id);
    const levels = data as Level[];
    
    // 将URL slug转换回显示名称
    const displayCategory = slugToCategoryName(category);
    
    // 查找匹配的关卡（使用分类slug匹配）
    const level = levels.find(l => 
      l.id === levelId && 
      categoryNameToSlug(l.category) === category
    );
    
    if (!level) {
      notFound();
    }

    // 获取当前关卡在所属分类中的位置
    const categoryLevels = levels.filter(l => 
      categoryNameToSlug(l.category) === category
    );
    const idx = categoryLevels.findIndex(l => l.id === levelId);
    
    // 查找前后关卡（同分类内）
    const prev = idx > 0 ? categoryLevels[idx - 1] : null;
    const next = idx < categoryLevels.length - 1 ? categoryLevels[idx + 1] : null;

    return (
      <div className="min-h-screen bg-gray-50">
        <div className="max-w-7xl mx-auto px-4 py-8">
          {/* 页面标题 */}
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            Perfect Tidy Level {level.id}
          </h1>
          
          {/* 分类信息 */}
          <div className="flex items-center mb-6">
            <span className="text-lg text-blue-600 font-medium">
              Category: {level.category}
            </span>
            <span className="mx-2 text-gray-400">•</span>
            <span className="text-sm text-gray-500">
              URL: /levels/{category}/{level.id}
            </span>
          </div>
          
          {/* 关卡内容 */}
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold mb-4">{level.title}</h2>
            
            {/* 导航 */}
            <div className="flex justify-between mt-6">
              {prev && (
                <a 
                  href={`/levels/${category}/${prev.id}`}
                  className="text-blue-600 hover:text-blue-800"
                >
                  ← Previous: Level {prev.id}
                </a>
              )}
              {next && (
                <a 
                  href={`/levels/${category}/${next.id}`}
                  className="text-blue-600 hover:text-blue-800 ml-auto"
                >
                  Next: Level {next.id} →
                </a>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  } catch (error) {
    notFound();
  }
}